import Vue from 'vue'
import App from '@/App.vue'
import vClickOutside from 'v-click-outside';
import router from '@/router';
import store from '@/store';


Vue.use(vClickOutside);
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
